import Vue from 'vue';
import App from './App.vue';
import VueRouter from "vue-router"
import Router from "./router/index.js";
import Mask from 'common/plugins/mask/index.js';

Vue.config.productionTip = true;

Vue.use(VueRouter);
Vue.use(Mask);

new Vue({
	render: (h) => h(App),
	beforeCreate() {
		Vue.prototype.$bus = this;
	},
	router: Router
}).$mount("#app");
